<template>
  <div class="register">
    <Form
      ref="formValidate"
      :model="formValidate"
      :rules="ruleValidate">
      <FormItem prop="name">
        <Input
          v-model="formValidate.name"
          placeholder="请输入用户名" />
      </FormItem>
      <FormItem prop="email">
        <Input
          v-model="formValidate.email"
          placeholder="请输入邮箱" />
      </FormItem>
      <FormItem prop="password">
        <Input
          v-model="formValidate.password"
          type="password"
          placeholder="请输入密码" />
      </FormItem>
      <FormItem prop="rePassword">
        <Input
          v-model="formValidate.rePassword"
          type="password"
          placeholder="确认密码" />
      </FormItem>
      <FormItem>
        <div class="error">
          <span v-show="errorNews">
            {{ errorNews }}
          </span>
          <p>
            已经有账户?
            <a @click="switchType('signIn')">登录</a>
          </p>
        </div>
        <Button
          type="primary"
          long
          class="land"
          @click="handleSubmit('formValidate')">
          注册
        </Button>
      </FormItem>
    </Form>
  </div>
</template>

<script>
import { ajax } from '@/util/ajax'
export default {
  props: {
    isAddUserModalShow: {
      default: false,
      type: Boolean
    }
  },
  data() {
    // 校验密码
    const validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码!'))
      } else {
        if (this.formValidate.rePassword !== '') {
          // 对第二个密码框单独验证
          this.$refs.formValidate.validateField('rePassword')
        }
        callback()
      }
    }
    // 校验两个密码是否一致
    const validatePassCheck = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请重新输入密码!'))
      } else if (value !== this.formValidate.password) {
        callback(new Error('两个密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      formValidate: {
        name: '',
        email: '',
        password: '',
        rePassword: ''
      },
      ruleValidate: {
        name: [{ required: true, message: '姓名不能为空', trigger: 'blur' }],
        email: [{ required: true, message: '项目名称不能为空', trigger: 'blur' }],
        password: [{ required: true, validator: validatePass, trigger: 'blur' }],
        rePassword: [{ required: true, validator: validatePassCheck, trigger: 'blur' }]
      },
      errorNews: ''
    }
  },
  watch: {},
  mounted() {},
  methods: {
    /**
     * 提交
     */
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          const formValidate = this.formValidate
          ajax({
            urlKey: '/api/user/register',
            methods: 'POST',
            data: {
              name: formValidate.name,
              email: formValidate.email,
              pass: formValidate.password,
              rePass: formValidate.rePassword
            }
          }).then(res => {
            if (res.status === 1) {
              this.switchType()
            } else {
              this.$Message.error(res.message)
            }
          })
        }
      })
    },
    switchType() {
      this.$emit('switch-type', 'signIn')
    }
  }
}
</script>

